"use client"

import { use } from "react"
import { useRouter } from "next/navigation"
import { useEffect, useState } from "react"
import { Button } from "@/components/shadcn/ui/button"
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/shadcn/ui/card"
import { Input } from "@/components/shadcn/ui/input"
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableFooter,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/shadcn/ui/table"
import {
  Pagination,
  PaginationContent,
  PaginationEllipsis,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/shadcn/ui/pagination"
import { FilterIcon } from "lucide-react"
import {
  Dialog,
  DialogClose,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/shadcn/ui/dialog"
import { AlertError } from "@/components/alertError"
import { Label } from "@/components/shadcn/ui/label"

const staticTypes = ["order", "noun"]
import { PlusCircle, Trash2, X, Plus, Save } from "lucide-react";

const TYPES = [
  { code: "en", name: "English" },
  { code: "vi", name: "Tiếng Việt" },
]

export default function ClientPage({ id, title }: { id: string; title: string }) {
  const router = useRouter()
  const [data, setData] = useState<any[]>([])

  const [error, setError] = useState<string | null>(null)
  const [libraryForm, setlibraryForm] = useState({ title: "", language: "" })

  const [newWordForm, setNewWordForm] = useState({
    word: "",
    pronunciation: "",
    types: [
      {
        type: "",
        meanings: [""],
        notes: [""],
      },
    ],
  })

  useEffect(() => {
    const newData = Array.from({ length: 20 }, (_, i) => {
      const shuffled = [...staticTypes].sort(() => 0.5 - Math.random())
      const selected = shuffled.slice(0, Math.floor(Math.random() * 3) + 1)

      return {
        _id: i,
        word: `Từ vựng ${i + 1}`,
        pronunciation: `Phát âm ${i + 1}`,
        types: selected.map((type) => ({
          type,
          meanings: [
            `Dịch nghĩa 1 của ${type.toLowerCase()} ${i + 1}`,
            `Dịch nghĩa 2 của ${type.toLowerCase()} ${i + 1}`,
          ],
          notes: [
            `Ghi chú 1 cho ${type.toLowerCase()} ${i + 1}`,
            `Ghi chú 2 cho ${type.toLowerCase()} ${i + 1}`,
          ],
        })),
      }
    })

    setData(newData)
  }, [])

  const handleChangeLibraryForm = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setlibraryForm({ ...libraryForm, [e.target.name]: e.target.value })
  }

  const handleAddLibrary = async (e: React.FormEvent) => {
    e.preventDefault()
    const { title, language } = libraryForm

    try {
      const res = await fetch("/api/dictionary/library", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ title, language }),
      })
      const data = await res.json()
      if (res.ok) {
        setlibraryForm({ title: "", language: "" })
        alert("Thêm thư viện thành công")
      } else {
        setError(data.message || "Lỗi khi thêm thư viện")
      }
    } catch (error) {
      console.error("Error adding library:", error)
      setError("Lỗi khi thêm thư viện. Vui lòng thử lại sau.")
    }
  }

  const handleWordFormChange = (
  e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>,
  typeIndex?: number,
  field?: "type", // <-- chỉ rõ "type"
  subIndex?: number,
  subField?: "meanings" | "notes"
) => {
  const { value } = e.target;
  if (typeof typeIndex === "number" && subField) {
    const updatedTypes = [...newWordForm.types];
    updatedTypes[typeIndex][subField][subIndex!] = value;
    setNewWordForm({ ...newWordForm, types: updatedTypes });
  } else if (typeof typeIndex === "number" && field) {
    const updatedTypes = [...newWordForm.types];
    updatedTypes[typeIndex][field] = value;
    setNewWordForm({ ...newWordForm, types: updatedTypes });
  } else {
    setNewWordForm({ ...newWordForm, [e.target.name]: value });
  }
};

const removeType = (typeIndex: number) => {
  const updatedTypes = [...newWordForm.types];
  updatedTypes.splice(typeIndex, 1);
  setNewWordForm({ ...newWordForm, types: updatedTypes });
};

const removeSubField = (typeIndex: number, subField: "meanings" | "notes", subIndex: number) => {
  const updatedTypes = [...newWordForm.types];
  updatedTypes[typeIndex][subField].splice(subIndex, 1);
  setNewWordForm({ ...newWordForm, types: updatedTypes });
};


  const addType = () => {
    setNewWordForm({
      ...newWordForm,
      types: [...newWordForm.types, { type: "", meanings: [""], notes: [""] }],
    })
  }

  const addSubField = (index: number, subField: "meanings" | "notes") => {
    const updatedTypes = [...newWordForm.types]
    updatedTypes[index][subField].push("")
    setNewWordForm({ ...newWordForm, types: updatedTypes })
  }

  const handleAddWord = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      const res = await fetch("/api/dictionary/word", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ libraryId: id, ...newWordForm }),
      })
      const data = await res.json()
      if (res.ok) {
        alert("Thêm từ vựng thành công")
        setNewWordForm({
          word: "",
          pronunciation: "",
          types: [{ type: "", meanings: [""], notes: [""] }],
        })
      } else {
        setError(data.message || "Lỗi khi thêm từ vựng")
      }
    } catch (err) {
      console.error(err)
      setError("Lỗi khi thêm từ vựng")
    }
  }

  return (
    <>
      <div className="text-end mb-4 space-x-2">
        <Button variant="destructive" onClick={() => router.back()}>
          Quay lại
        </Button>

        {/* Dialog: Thêm thư viện */}
        <Dialog>
          <DialogTrigger asChild>
            <Button variant="outline">Thêm thư viện</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[425px]">
            <DialogHeader>
              <DialogTitle>Thêm thư viện</DialogTitle>
              <DialogDescription>
                Nhập thông tin thư viện. Nhấn "Lưu" để tạo mới.
              </DialogDescription>
            </DialogHeader>
            <form onSubmit={handleAddLibrary}>
              {error && <AlertError>{error}</AlertError>}
              <div className="grid gap-4 py-4">
                <div className="grid gap-3">
                  <Label htmlFor="title">Tên thư viện</Label>
                  <Input
                    name="title"
                    value={libraryForm.title}
                    onChange={handleChangeLibraryForm}
                    required
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="language">Ngôn ngữ</Label>
                  <select
                    name="language"
                    value={libraryForm.language}
                    onChange={handleChangeLibraryForm}
                    className="w-full px-3 py-2 border rounded-md text-sm"
                    required
                  >
                    <option value="" disabled>
                      Chọn ngôn ngữ
                    </option>
                    {TYPES.map((lang) => (
                      <option key={lang.code} value={lang.code}>
                        {lang.name}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              <DialogFooter>
                <DialogClose asChild>
                  <Button type="button" variant="outline">
                    Hủy
                  </Button>
                </DialogClose>
                <Button type="submit">Lưu</Button>
              </DialogFooter>
            </form>
          </DialogContent>
        </Dialog>

        {/* Dialog: Thêm từ vựng */}
<Dialog>
  <DialogTrigger asChild>
    <Button variant="outline" className="gap-2">
      <PlusCircle className="w-4 h-4" />
      Thêm từ vựng
    </Button>
  </DialogTrigger>
  <DialogContent className="max-w-2xl max-h-[90vh] overflow-auto">
    <DialogHeader>
      <DialogTitle className="text-2xl">Thêm từ vựng mới</DialogTitle>
      <DialogDescription>
        Nhập đầy đủ thông tin từ vựng bên dưới
      </DialogDescription>
    </DialogHeader>
    
    <form onSubmit={handleAddWord} className="mt-4">
      <div className="space-y-6">
        {/* Word and Pronunciation */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label htmlFor="word" className="font-medium">
              Từ vựng *
            </Label>
            <Input
              id="word"
              name="word"
              value={newWordForm.word}
              onChange={handleWordFormChange}
              required
              placeholder="Nhập từ vựng"
              className="h-10"
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="pronunciation" className="font-medium">
              Phát âm
            </Label>
            <Input
              id="pronunciation"
              name="pronunciation"
              value={newWordForm.pronunciation}
              onChange={handleWordFormChange}
              placeholder="Nhập phiên âm"
              className="h-10"
            />
          </div>
        </div>

        {/* Word Types */}
        <div className="space-y-4">
          {newWordForm.types.map((t, typeIndex) => (
            <div
              key={typeIndex}
              className="border p-4 rounded-lg bg-gray-50/50 dark:bg-gray-800/50 space-y-4"
            >
              <div className="flex justify-between items-center">
                <h4 className="font-medium text-lg">
                  Loại từ {typeIndex + 1}
                </h4>
                {typeIndex > 0 && (
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    onClick={() => removeType(typeIndex)}
                    className="text-red-500 hover:text-red-600"
                  >
                    <Trash2 className="w-4 h-4" />
                  </Button>
                )}
              </div>

              <div className="space-y-3">
                <div className="space-y-2">
                  <Label>Loại từ *</Label>
                  <Input
                    placeholder="Danh từ/Động từ/Tính từ..."
                    value={t.type}
                    onChange={(e) =>
                      handleWordFormChange(e, typeIndex, "type")
                    }
                    required
                  />
                </div>

                {/* Meanings */}
                <div className="space-y-2">
                  <Label>Dịch nghĩa</Label>
                  <div className="space-y-2">
                    {t.meanings.map((m, i) => (
                      <div key={i} className="flex gap-2 items-center">
                        <Input
                          placeholder={`Nghĩa ${i + 1}`}
                          value={m}
                          onChange={(e) =>
                            handleWordFormChange(
                              e,
                              typeIndex,
                              undefined,
                              i,
                              "meanings"
                            )
                          }
                        />
                        {i > 0 && (
                          <Button
                            type="button"
                            variant="ghost"
                            size="icon"
                            onClick={() => removeSubField(typeIndex, "meanings", i)}
                            className="text-red-500 hover:text-red-600"
                          >
                            <X className="w-4 h-4" />
                          </Button>
                        )}
                      </div>
                    ))}
                  </div>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={() => addSubField(typeIndex, "meanings")}
                    className="gap-1"
                  >
                    <Plus className="w-3 h-3" />
                    Thêm nghĩa
                  </Button>
                </div>

                {/* Notes */}
                <div className="space-y-2">
                  <Label>Ghi chú</Label>
                  <div className="space-y-2">
                    {t.notes.map((n, i) => (
                      <div key={i} className="flex gap-2 items-center">
                        <Input
                          placeholder={`Ghi chú ${i + 1}`}
                          value={n}
                          onChange={(e) =>
                            handleWordFormChange(e, typeIndex, undefined, i, "notes")
                          }
                        />
                        {i > 0 && (
                          <Button
                            type="button"
                            variant="ghost"
                            size="icon"
                            onClick={() => removeSubField(typeIndex, "notes", i)}
                            className="text-red-500 hover:text-red-600"
                          >
                            <X className="w-4 h-4" />
                          </Button>
                        )}
                      </div>
                    ))}
                  </div>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={() => addSubField(typeIndex, "notes")}
                    className="gap-1"
                  >
                    <Plus className="w-3 h-3" />
                    Thêm ghi chú
                  </Button>
                </div>
              </div>
            </div>
          ))}
        </div>

        <Button
          type="button"
          variant="outline"
          onClick={addType}
          className="gap-2 w-full"
        >
          <Plus className="w-4 h-4" />
          Thêm loại từ
        </Button>
      </div>

      <DialogFooter className="mt-6">
        <DialogClose asChild>
          <Button type="button" variant="outline">
            Hủy bỏ
          </Button>
        </DialogClose>
        <Button type="submit" className="gap-2">
          <Save className="w-4 h-4" />
          Lưu từ vựng
        </Button>
      </DialogFooter>
    </form>
  </DialogContent>
</Dialog>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Thư viện: {title}</CardTitle>
        </CardHeader>

        <CardContent>
          <Table>
            <TableCaption>Danh sách từ vựng trong thư viện.</TableCaption>
            <TableHeader>
              <TableRow>
                <TableHead>#</TableHead>
                <TableHead>Từ vựng</TableHead>
                <TableHead>Phát âm</TableHead>
                <TableHead>Loại từ</TableHead>
                <TableHead>Dịch nghĩa</TableHead>
                <TableHead>Ghi chú</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {data.map((item, index) =>
                item.types.map((t: any, typeIndex: number) => {
                  const isFirstRow = typeIndex === 0
                  return (
                    <TableRow key={`${item._id}-${typeIndex}`}>
                      <TableCell>{isFirstRow ? index + 1 : ""}</TableCell>
                      <TableCell>{isFirstRow ? item.word : ""}</TableCell>
                      <TableCell>
                        {isFirstRow ? item.pronunciation : ""}
                      </TableCell>
                      <TableCell>{t.type}</TableCell>
                      <TableCell>
                        <ul className="list-disc pl-4">
                          {t.meanings.map((m: string, i: number) => (
                            <li key={i}>{m}</li>
                          ))}
                        </ul>
                      </TableCell>
                      <TableCell>
                        <ul className="list-disc pl-4">
                          {t.notes.map((n: string, i: number) => (
                            <li key={i}>{n}</li>
                          ))}
                        </ul>
                      </TableCell>
                    </TableRow>
                  )
                })
              )}
            </TableBody>
            <TableFooter>
              <TableRow>
                <TableCell colSpan={6}>
                  Tổng cộng: {data.reduce((acc, d) => acc + d.types.length, 0)}{" "}
                  mục
                </TableCell>
              </TableRow>
            </TableFooter>
          </Table>
        </CardContent>

        <CardFooter>
          <Pagination>
            <PaginationContent>
              <PaginationItem>
                <PaginationPrevious href="#" />
              </PaginationItem>
              <PaginationItem>
                <PaginationLink href="#">1</PaginationLink>
              </PaginationItem>
              <PaginationItem>
                <PaginationLink href="#" isActive>
                  2
                </PaginationLink>
              </PaginationItem>
              <PaginationItem>
                <PaginationLink href="#">3</PaginationLink>
              </PaginationItem>
              <PaginationItem>
                <PaginationEllipsis />
              </PaginationItem>
              <PaginationItem>
                <PaginationNext href="#" />
              </PaginationItem>
            </PaginationContent>
          </Pagination>
        </CardFooter>
      </Card>
    </>
  )
}
